body {margin:0;padding: 0}
.App {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
  height: 100%;
  position: absolute;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-header {
  background-color: #222;
  height: 60px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  color: #d87878;
}

.App-container {
  height: 100%;
  background: #dadada;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.tips {
  position: fixed;
  top: 70px;
  right: 100px;
  border: 5px;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}
.tips ul li {
  margin: 10px 0px;
}

.tips ul li:nth-of-type(1) { -webkit-animation: xooom 3s ease-in 1s; animation: xooom 3s ease-in 1s;}
.tips ul li:nth-of-type(2) { -webkit-animation: xooom 3s ease-in 1s; animation: xooom 3s ease-in 1s;}
.tips ul li:nth-of-type(3) { -webkit-animation: xooom 3s ease-in 4.5s; animation: xooom 3s ease-in 4.5s;}
.tips ul li:nth-of-type(4) { -webkit-animation: xooom 3s ease-in 8s; animation: xooom 3s ease-in 8s;}
.tips ul li:nth-of-type(5) { -webkit-animation: xooom 3s ease-in 14s; animation: xooom 3s ease-in 14s;}
.tips h3:nth-of-type(2) { -webkit-animation: xooom 3s ease-in 16s; animation: xooom 3s ease-in 16s;}

@-webkit-keyframes xooom {
  from {
    border: solid 3px white;
  }
  to {
    border: solid 5px #d87878;
  }
}

@keyframes xooom {
  from {
    border: solid 3px white;
  }
  to {
    border: solid 5px #d87878;
  }
}

.board {
  background: #fff;
  border-radius: 5px;
  max-width: 600px;
  min-height: 600px;
  height: 600px;
  display: -ms-flexbox;
  display: flex;
  border: solid 3px #333;
  -ms-flex-flow: column;
      flex-flow: column;
  -webkit-animation-name: flipBoard;
          animation-name: flipBoard;
  -webkit-animation-duration: 100s;
          animation-duration: 100s;
  -webkit-animation-delay: 10s;
          animation-delay: 10s;
}

@-webkit-keyframes flipBoard {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  3% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }
  100% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }
}

@keyframes flipBoard {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  3% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }
  100% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }
}

.box-row {
  display: -ms-flexbox;
  display: flex;
}
.box-row:nth-child(2n) {
  -webkit-animation-name: reverseRows;
          animation-name: reverseRows;
  -webkit-animation-duration: 100s;
          animation-duration: 100s;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}

@-webkit-keyframes reverseRows {
  0% {
    flex-direction: row-reverse;
  }
  3% {
    flex-direction: row-reverse;
  }
  100% {
    flex-direction: row-reverse;
  }
}

@keyframes reverseRows {
  0% {
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
  }
  3% {
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
  }
  100% {
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
  }
}

.box {
  width: 59px;
  height: 59px;
  border: solid 0.5px #d87878;
  text-align: left;
  vertical-align: text-top;
  -webkit-transition: 0.2s ease-in;
  -o-transition: 0.2s ease-in;
  transition: 0.2s ease-in;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -webkit-animation-name: loadingBox;
          animation-name: loadingBox;
  -webkit-animation-duration: 100s;
          animation-duration: 100s;
  -webkit-animation-delay: 16s;
          animation-delay: 16s;
}

@-webkit-keyframes loadingBox {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  5% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }

  100% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }
}

@keyframes loadingBox {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  5% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }

  100% {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1)
  }
}

.box span {
  position: absolute;
  top: 5px;
  left: 5px;
  -webkit-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
  transition: 0.3s ease-in;
}
.box:hover span {
  top: 22px;
  left: 20px;
  font-weight: bold;
  -webkit-transition: 0.1s ease-in;
  -o-transition: 0.1s ease-in;
  transition: 0.1s ease-in;
}

.box:nth-child(2n) {
  background: #d87878;
  border: solid 0.5px #fff;
}


